AC1B;AACF;;AAEA,6BAA6B;AAC7B;EACE,6DAA6D;EAC7D,cAAc;EACd,kBAAkB;EAClB,mBAAmB;EACnB,cAAc;EACd,gBAAgB;EAChB,yBAAyB;EACzB,4CAA4C;AAC9C;;AAEA;EACE,6DAA6D;EAC7D,cAAc;EACd,kBAAkB;EAClB,mBAAmB;EACnB,cAAc;EACd,gBAAgB;EAChB,yBAAyB;EACzB,6CAA6C;AAC/C","sourcesContent":["/* WeeklyPlanModal.css - Ultra Modern Styling */\r\n:root {\r\n  --ocean-primary: #0EA5E9;\r\n  --ocean-secondary: #0284C7;\r\n  --ocean-dark: #0369A1;\r\n  --ocean-light: #7DD3FC;\r\n  --ocean-accent: #06B6D4;\r\n  --success-green: #10B981;\r\n  --danger-red: #EF4444;\r\n  --warning-orange: #F59E0B;\r\n}\r\n\r\n* {\r\n  box-sizing: border-box;\r\n}\r\n\r\n/* Main Container */\r\n.weekly-plan-main-container {\r\n  background: linear-gradient(135deg, #0EA5E9 0%, #0284C7 50%, #0369A1 100%);\r\n  position: relative;\r\n  min-height: 100vh;\r\n  padding: 20px;\r\n  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;\r\n}\r\n\r\n/* Header Section */\r\n.weekly-plan-header {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  background: rgba(255, 255, 255, 0.95);\r\n  backdrop-filter: blur(15px);\r\n  padding: 20px 30px;\r\n  border-radius: 20px;\r\n  margin-bottom: 25px;\r\n  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.1);\r\n  border: 1px solid rgba(255, 255, 255, 0.2);\r\n  flex-wrap: wrap;\r\n  gap: 15px;\r\n}\r\n\r\n.weekly-plan-buttons-group {\r\n  display: flex;\r\n  gap: 12px;\r\n  align-items: center;\r\n  flex-wrap: wrap;\r\n}\r\n\r\n/* Modern Buttons */\r\n.weekly-plan-btn {\r\n  background: linear-gradient(135deg, #0EA5E9 0%, #0284C7 100%);\r\n  color: white;\r\n  border: none;\r\n  padding: 12px 24px;\r\n  border-radius: 12px;\r\n  font-weight: 600;\r\n  cursor: pointer;\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 8px;\r\n  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);\r\n  box-shadow: 0 4px 15px rgba(14, 165, 233, 0.3);\r\n  font-size: 14px;\r\n  text-transform: uppercase;\r\n  letter-spacing: 0.5px;\r\n}\r\n\r\n.weekly-plan-btn:hover {\r\n  transform: translateY(-3px);\r\n  box-shadow: 0 8px 25px rgba(14, 165, 233, 0.4);\r\n  filter: brightness(1.1);\r\n  transform: translateY(-2px);\r\n  background: linear-gradient(135deg, #0284C7 0%, #0369A1 100%);\r\n\r\n}\r\n\r\n.weekly-plan-btn:active {\r\n  transform: translateY(-1px);\r\n}\r\n\r\n.weekly-plan-btn-primary {\r\n  background: linear-gradient(135deg, #0891B2 0%, #0E7490 100%);\r\n  box-shadow: 0 8px 25px rgba(6, 182, 212, 0.4);\r\n}\r\n\r\n.weekly-plan-btn-primary:hover {\r\n  background: linear-gradient(135deg, #329ffc 0%, #00d4ff 100%);\r\n  box-shadow: 0 8px 25px rgba(79, 172, 254, 0.4);\r\n}\r\n\r\n.weekly-plan-btn-success {\r\n  background: linear-gradient(135deg, #48bb78 0%, #38a169 100%);\r\n  box-shadow: 0 4px 15px rgba(72, 187, 120, 0.3);\r\n  padding: 14px 28px;\r\n  font-size: 16px;\r\n}\r\n\r\n.weekly-plan-btn-success:hover {\r\n  background: linear-gradient(135deg, #059669 0%, #047857 100%);\r\n  box-shadow: 0 8px 25px rgba(16, 185, 129, 0.4);\r\n}\r\n\r\n/* Week Navigation */\r\n.weekly-plan-week-nav {\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  gap: 20px;\r\n  background: rgba(255, 255, 255, 0.95);\r\n  backdrop-filter: blur(15px);\r\n  padding: 20px;\r\n  border-radius: 20px;\r\n  margin-bottom: 25px;\r\n  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.1);\r\n  border: 1px solid rgba(255, 255, 255, 0.2);\r\n  flex-wrap: wrap;\r\n}\r\n\r\n.weekly-plan-week-title {\r\n  font-size: 20px;\r\n  font-weight: 700;\r\n  color: #2d3748;\r\n  text-align: center;\r\n  flex: 1;\r\n  min-width: 200px;\r\n}\r\n\r\n.weekly-plan-week-status {\r\n  font-size: 14px;\r\n  color: #718096;\r\n  margin-top: 5px;\r\n  font-weight: 500;\r\n}\r\n\r\n.weekly-plan-nav-btn {\r\n  background: linear-gradient(135deg, #CBD5E1 0%, #94A3B8 100%);\r\n  color: #1E293B;\r\n  border: none;\r\n  padding: 12px 20px;\r\n  border-radius: 12px;\r\n  font-weight: 600;\r\n  cursor: pointer;\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 8p